"use client";

import React, { useRef } from "react";
import Image from "next/image";
import { ScrollArea } from "@/components/ui/scroll-area";
import { FaAngleRight, FaChevronLeft, FaChevronRight, FaPlayCircle } from "react-icons/fa";
import { CiBoxList } from "react-icons/ci";
import Button from "@/animation/arrow";


const Collectio = () => {
    const movies = [
        {
            id: 1,
            title: "Batman",
            image: "/bat.jpg",
        },
        {
            id: 2,
            title: "Captain America",
            image: "/cp.jpg",
        },
        {
            id: 3,
            title: "Hulk",
            image: "/HULK.jpg",
        },
        {
            id: 4,
            title: "transformers",
            image: "/transformer.jpg",
        },
        {
            id: 5,
            title: "Loki",
            image: "/loki.jpg",
        },
        {
            id: 6,
            title: "Spiderman",
            image: "/spider.jpg",
        },
    ];
  const scrollAreaRef = useRef<HTMLDivElement | null>(null);

  const getViewport = () =>
    scrollAreaRef.current?.querySelector(
      "[data-radix-scroll-area-viewport]"
    ) as HTMLDivElement | null;

  const scrollLeft = () => {
    getViewport()?.scrollBy({ left: -300, behavior: "smooth" });
  };

  const scrollRight = () => {
    getViewport()?.scrollBy({ left: 300, behavior: "smooth" });
  };

  return (
    <div className="pt-[1rem] w-full">
      {/* Title */}
      <div className="px-6 sm:px-10 md:px-20 mb-4">
        <h1 className="text-2xl font-bold text-white flex items-center gap-2">Gems for you <FaAngleRight /></h1>
      </div>

      {/* Wrapper */}
      <div
        ref={scrollAreaRef}
        className="relative px-6 sm:px-10 md:px-20 group overflow-hidden"
      >
        {/* LEFT BUTTON */}
        <button
          onClick={scrollLeft}
          className="hidden md:flex absolute left-4 top-1/2 -translate-y-1/2 z-30
          bg-black/60 hover:bg-black text-white p-2 rounded-full
          opacity-0 group-hover:opacity-100 transition"
        >
          <FaChevronLeft size={22} />
        </button>

        {/* RIGHT BUTTON */}
        <button
          onClick={scrollRight}
          className="hidden md:flex absolute right-4 top-1/2 -translate-y-1/2 z-30
          bg-black/60 hover:bg-black text-white p-2 rounded-full
          opacity-0 group-hover:opacity-100 transition"
        >
          <FaChevronRight size={22} />
        </button>

        {/* SHADCN SCROLL AREA */}
        <ScrollArea ref={scrollAreaRef} className="w-full">
          <div className="flex gap-2 py-10 px-8">
            {movies.map((movie) => (
              <div
                key={movie.id}
                className="
                  relative min-w-[250px] h-[150px] rounded-lg
                  transition-transform duration-300 ease-out
                  origin-bottom hover:scale-125 hover:z-50
                  cursor-pointer
                "
              >
                <Image
                  src={movie.image}
                  alt="movie"
                  fill
                  className="object-cover rounded-lg"
                />

                {/* Overlay */}
                <div className="absolute inset-0 bg-gradient-to-t
                  from-black/90 via-black/50 to-transparent
                  opacity-0 hover:opacity-100 transition"
                />

                {/* Details */}
                <div className="absolute bottom-3 left-3 right-3
                  opacity-0 hover:opacity-100
                  transition-all duration-300
                  translate-y-4 hover:translate-y-0">
                  <h3 className="text-white text-sm font-semibold">
                    {movie.title}
                  </h3>

                  <div className="flex gap-2 mt-2">
                    <button className="bg-white text-black p-1 rounded-full hover:bg-[#e50914] hover:text-white transition">
                      <FaPlayCircle size={18} />
                    </button>
                    <button className="bg-gray-700 text-white p-1 rounded-full hover:bg-gray-900 transition">
                      <CiBoxList size={18} />
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </ScrollArea>
      </div>
    </div>
  );
};

export default Collectio;




